.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.PopModal {
  font-size: 12px;
  max-width: 100% !important;
  max-height: 100% !important;
}
.PopModal > .header {
  width: 100%;
  border-bottom: 1px solid gray;
  font-size: 18px;
  text-align: center;
  padding: 5px;
}
.PopModal > .content{
  width: 100%;
  padding: 10px 5px;
  max-width: 100% !important;
  max-height: 480px !important;
}
.PopModal > .actions {
  width: 100%;
  padding: 10px 5px;
  margin: auto;
  text-align: center;
}
.PopModal > .close {
  cursor: pointer;
  position: absolute;
  display: block;
  padding: 2px 5px;
  line-height: 20px;
  right: -10px;
  top: -10px;
  font-size: 24px;
  background: #ffffff;
  border-radius: 18px;
  border: 1px solid #cfcece;
}

.PopModal canvas{
  max-width: 100% !important;
  max-height: 400px !important;
}

.PopButton{
  font-family: Roboto,Arial,sans-serif;
  color: #000;
  cursor: pointer;
  padding: 0 12px;
  display: inline-block;
  margin: 0px 15px;
  line-height: 1.8em;
  letter-spacing: 1.5px;
  font-size: 1em;
  outline: none;
  position: relative;
  font-size: 12px;
  border: 3px solid #cfcece;
  background-color: #fff;
  border-radius: 15px 15px 15px 15px;
  transition: all .3s;
}
.popup-content {
  width: 90% !important;
  max-height: 510px !important;
}
