.UpFile {
    text-align: center;
    background-color: rgb(206, 213, 223);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
  }
  
  .Card {
    background-color: white;
    padding: 32px;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    box-shadow: 0 15px 30px 0 rgba(0, 0, 0, 0.11), 0 5px 15px 0 rgba(0, 0, 0, 0.08);
    box-sizing: border-box;
  }
  .jumbo {
    /*background: url(${bg}) no-repeat fixed bottom;*/
    background: url(../images/bgHomePage.png) no-repeat fixed bottom;
    background-size: cover;
    height: 360px;
    position: relative;
    z-index: -2;
  }
  .overlay {
  
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
  }
